html,
body {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

body {
  color: #393939;
  font-size: 16px;
  line-height: 18px;
  font-family: "Ubuntu", sans-serif;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  padding: 0 40px;
  margin: 0 auto;
}
@media (min-width: 744px) {
  .container {
    padding-left: 60px;
    padding-right: 60px;
  }
}
@media (min-width: 1000px) {
  .container {
    max-width: 1171px;
    padding-left: 30px;
    padding-right: 30px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.accent {
  font-size: 20px;
  line-height: 23px;
  font-weight: 700;
  letter-spacing: -0.03em;
}
@media (min-width: 744px) {
  .accent {
    font-size: 26px;
    line-height: 30px;
  }
}
@media (min-width: 1000px) {
  .accent {
    font-size: 36px;
    line-height: 41px;
  }
}

.accent-less {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
}
@media (min-width: 1000px) {
  .accent-less {
    font-size: 24px;
    line-height: 28px;
  }
}

@font-face {
  font-family: "Ubuntu";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Ubuntu-Bold.woff2") format("woff2"), url("../fonts/Ubuntu-Bold.woff") format("woff");
}
@font-face {
  font-family: "Ubuntu";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Ubuntu-Regular.woff2") format("woff2"), url("../fonts/Ubuntu-Regular.woff") format("woff");
}
.header__container {
  padding-top: 40px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1000px) {
  .header__container {
    padding-top: 50px;
  }
}

.header__logo {
  width: 40px;
  height: 40px;
}
@media (min-width: 744px) {
  .header__logo {
    width: 60px;
    height: 60px;
  }
}

.header__list {
  position: absolute;
  right: 30px;
  top: 40px;
  display: none;
  flex-direction: column;
  margin: 0;
  padding: 28px 20px;
  width: 150px;
  height: 150px;
  background-color: #393939;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(2px);
  border-radius: 5px;
  color: #ffffff;
  z-index: 10;
  list-style: none;
}
@media (min-width: 744px) {
  .header__list {
    display: flex;
    position: static;
    padding: 0;
    width: auto;
    height: auto;
    box-shadow: none;
    background-color: inherit;
    color: #393939;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    flex-direction: row;
  }
}

.header__list li {
  margin-bottom: 20px;
}
.header__list li:last-child {
  margin-bottom: 0;
}
@media (min-width: 744px) {
  .header__list li {
    margin-bottom: 0;
    margin-right: 40px;
  }
  .header__list li:last-child {
    margin-right: 0;
  }
}

.header__list a {
  position: relative;
  text-decoration: none;
  color: inherit;
  font-weight: 700;
}
.header__list a::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 5%;
  display: block;
  height: 2px;
  width: 0;
  background-color: #393939;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
}
@media (min-width: 1000px) {
  .header__list a:hover::after, .header__list a:focus::after {
    width: 100%;
    left: 0;
    cursor: pointer;
  }
}

.header__burger {
  display: flex;
  border: 0;
  padding: 0;
  background-color: inherit;
  cursor: pointer;
}
@media (min-width: 744px) {
  .header__burger {
    display: none;
  }
}

.header__burger-svg {
  width: 30px;
  height: 27px;
}

.header__close {
  display: none;
  position: absolute;
  right: 30px;
  top: 40px;
  padding: 0;
  width: 24px;
  height: 24px;
  border: 0;
  background-color: inherit;
  z-index: 20;
}

.header__close-svg {
  width: 24px;
  height: 24px;
  border-top-right-radius: 5px;
}

.header--open .header__burger {
  display: none;
}
.header--open .header__list,
.header--open .header__close {
  display: flex;
}

.promo {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-top: 56px;
}
@media (min-width: 744px) {
  .promo {
    padding-top: 193px;
  }
}
@media (min-width: 1000px) {
  .promo {
    padding-top: 164px;
    padding-left: 28px;
    padding-bottom: 44px;
  }
}

.promo h1 {
  width: 145px;
  margin: 0;
  padding-top: 23px;
  font-weight: 700;
  font-size: 36px;
  line-height: 41px;
  letter-spacing: -0.03em;
}
@media (min-width: 744px) {
  .promo h1 {
    width: auto;
    padding-top: 0;
    margin-bottom: 19px;
    font-size: 45px;
    line-height: 52px;
  }
}
@media (min-width: 1000px) {
  .promo h1 {
    padding-top: 0;
    margin-bottom: 25px;
    font-size: 55px;
    line-height: 63px;
  }
}

.promo__paragraph {
  grid-column: 1/-1;
  margin-top: 20px;
  margin-bottom: 40px;
}
.promo__paragraph:first-of-type {
  grid-row: 2/3;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: -0.03em;
}
.promo__paragraph:last-of-type {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  margin-top: 0;
  margin-bottom: 42px;
}
@media (min-width: 744px) {
  .promo__paragraph {
    grid-column: 1/2;
  }
  .promo__paragraph:first-of-type {
    margin-top: 0;
    margin-bottom: 29px;
    font-size: 26px;
    line-height: 30px;
  }
  .promo__paragraph:last-of-type {
    margin-bottom: 37px;
  }
}
@media (min-width: 1000px) {
  .promo__paragraph:first-of-type {
    margin-bottom: 33px;
    font-weight: 400;
    font-size: 36px;
    line-height: 41px;
  }
  .promo__paragraph:last-of-type {
    margin-bottom: 41px;
    font-size: 24px;
    line-height: 28px;
  }
}

.promo__link {
  display: inline-flex;
  align-items: center;
  grid-column: 1/-1;
  padding: 13px 14px;
  min-height: 50px;
  width: 193px;
  margin: 0 auto 45px;
  background: #393939;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  color: #ffffff;
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
}
@media (min-width: 744px) {
  .promo__link {
    margin: 0 0 100px;
    padding: 21px 16px;
    width: 210px;
    min-height: 60px;
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
  }
}
@media (min-width: 1000px) {
  .promo__link {
    grid-row: 4/5;
    padding: 17px 15px;
    padding-right: 12px;
    font-size: 16px;
    line-height: 18px;
  }
  .promo__link:hover {
    background-image: linear-gradient(0deg, #393939 0%, #595959 100%);
    box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.25);
  }
}

.promo__link svg {
  margin-right: 10px;
  width: 17px;
  height: 15px;
}

.promo__img {
  display: block;
  grid-column: 2/3;
  grid-row: 1/2;
  margin-left: 27px;
  width: 120px;
  height: 120px;
  border-radius: 20px;
  filter: drop-shadow(0px 15px 40px rgba(0, 0, 0, 0.25));
}
@media (min-width: 530px) {
  .promo__img {
    width: 140px;
    height: 140px;
  }
}
@media (min-width: 744px) {
  .promo__img {
    grid-row: 1/5;
    margin-left: auto;
    width: 250px;
    height: 250px;
  }
}
@media (min-width: 1000px) {
  .promo__img {
    margin: 10px 0 0 auto;
    height: 400px;
    width: 400px;
    border-radius: 24px;
  }
}

.footer__list {
  display: flex;
  width: 140px;
  justify-content: space-between;
  padding: 0;
  margin: 0 auto;
  list-style: none;
}
@media (min-width: 1000px) {
  .footer__list {
    margin: 0;
  }
}

.footer__copyright p {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0;
  margin-bottom: 81px;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #d9d9d9;
}

.footer__copyright a {
  color: #d9d9d9;
  text-decoration: none;
}

.footer__list {
  margin-bottom: 42px;
}
@media (min-width: 744px) {
  .footer__list {
    margin: 0 0 68px;
  }
}
@media (min-width: 1000px) {
  .footer__list {
    margin-bottom: 17px;
  }
}